@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter.ttf");
  font-style: normal;
}
.footer {
  display: none;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 72px;
  background-color: rgb(255, 255, 255);
  border-top: 1px solid rgba(0, 0, 0, 0.16);
  padding: 0 24px;
  align-items: center;
}
@media (max-width: 1199px) {
  .footer {
    display: flex;
  }
}
@media (max-width: 767px) {
  .footer {
    height: 76px;
    padding: 0 16px;
  }
}
.footer__nav-list {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 12px;
  padding: 0;
  margin: 0;
  list-style: none;
}

.footer-nav-element__link {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: nowrap;
  gap: 4px;
  height: 40px;
  padding: 0 16px;
  border-radius: 99px;
  text-decoration: none;
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  color: #2f2227;
  transition: background-color 0.2s;
}
@media (max-width: 767px) {
  .footer-nav-element__link {
    height: 60px;
    flex-direction: column;
    font-size: 12px;
    font-weight: 500;
    line-height: 16px;
  }
}
.footer-nav-element__link_active {
  background-color: rgba(0, 0, 0, 0.08);
}
@media (max-width: 767px) {
  .footer-nav-element__link_active {
    background-color: transparent;
    color: #d8598c;
  }
  .footer-nav-element__link_active .footer-nav-element {
    color: #d8598c;
  }
}
.footer-nav-element__link:hover:not(.footer-nav-element__link_active) {
  background-color: #f7dee8;
}
.footer-nav-element__link:active:not(.footer-nav-element__link_active) {
  background-color: #d8598c;
  color: #fff;
}
.footer-nav-element__link:active:not(.footer-nav-element__link_active) .footer-nav-element__icon {
  color: #fff;
}
.footer-nav-element__icon {
  flex-shrink: 0;
  width: 24px;
  height: 24px;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  height: 80px;
  display: flex;
  align-items: center;
  background-color: rgb(255, 255, 255);
}
.header__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 0 24px;
}
@media (max-width: 767px) {
  .header__content {
    padding: 0 16px;
  }
}
.header__logo {
  width: 241px;
  height: 40px;
}
.header__logo img {
  width: 100%;
}
.header__button {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  padding: 0;
  margin: 0;
  border: none;
  border-radius: 99px;
  background-color: transparent;
  color: #2f2227;
  cursor: pointer;
  transition: all 0.2s;
}
.header__button:focus {
  outline: none;
}
.header__button:hover {
  background-color: #f7dee8;
}
.header__button:active {
  background-color: #d8598c;
  color: #fff;
}

.nav {
  display: flex;
  margin-left: 64px;
  margin-right: auto;
}
@media (max-width: 1199px) {
  .nav {
    display: none;
  }
}
.nav__list {
  display: flex;
  flex-wrap: nowrap;
  list-style: none;
  padding: 0;
  margin: 0;
  gap: 12px;
}

.nav-element__link {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: nowrap;
  gap: 4px;
  height: 40px;
  padding: 0 16px;
  border-radius: 99px;
  text-decoration: none;
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  color: #2f2227;
  transition: background-color 0.2s;
}
.nav-element__link_active {
  background-color: rgba(0, 0, 0, 0.08);
}
.nav-element__link:hover:not(.nav-element__link_active) {
  background-color: #f7dee8;
}
.nav-element__link:active:not(.nav-element__link_active) {
  background-color: #d8598c;
  color: #fff;
}
.nav-element__link:active:not(.nav-element__link_active) .nav-element__icon {
  color: #fff;
}
.nav-element__icon {
  flex-shrink: 0;
  width: 24px;
  height: 24px;
  color: rgba(47, 34, 39, 0.8);
}

.select-component-wrapper {
  position: relative;
}
.select-component-wrapper .select-component {
  position: relative;
  min-width: 124px;
  padding: 0 12px;
  height: 44px;
  display: flex;
  align-items: center;
  border-radius: 99px;
  border: 2px solid rgba(0, 0, 0, 0.16);
  background: rgb(255, 255, 255);
  transition: border-color 0.2s;
}
.select-component-wrapper .select-component:hover {
  border-color: rgba(0, 0, 0, 0.24);
}
.select-component-wrapper .select-component:focus {
  outline: none;
  box-shadow: none;
}
.select-component-wrapper .select-component.ss-disabled {
  border-color: transparent;
  background-color: rgba(0, 0, 0, 0.04);
}
.select-component-wrapper .select-component.ss-disabled .ss-values {
  color: rgba(47, 34, 39, 0.8);
}
.select-component-wrapper .select-component.ss-disabled .ss-arrow path {
  fill: rgba(47, 34, 39, 0.8);
}
.select-component-wrapper .select-component__label {
  margin-bottom: 4px;
  color: rgba(47, 34, 39, 0.8);
  font-size: 14px;
  line-height: 20px;
}
.select-component-wrapper .select-component__reset {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 22px;
  height: 34px;
  top: 5px;
  right: 12px;
  padding: 0;
  border: none;
  cursor: pointer;
  background-color: transparent;
}
.select-component-wrapper .select-component__reset-icon {
  width: 16px;
  height: 16px;
  fill: #8f8f8f;
}
.select-component-wrapper .select-component .ss-values {
  color: #1f1f1f;
  font-size: 14px;
  line-height: 20px;
}
.select-component-wrapper .select-component .ss-values .ss-placeholder,
.select-component-wrapper .select-component .ss-values .ss-single {
  padding: 0;
  margin: 0;
  font-size: 16px;
  line-height: 24px;
}
.select-component-wrapper .ss-open-below {
  border-color: #66a6d4;
  border-radius: 22px 22px 0 0;
}
.select-component-wrapper .ss-open-below:hover {
  border-color: #66a6d4;
}
.select-component-wrapper .ss-arrow {
  flex-shrink: 0;
  width: 12px;
  height: 12px;
  margin-left: 12px;
  margin-right: 4px;
}
.select-component-wrapper .ss-arrow path {
  stroke: none;
  fill: #2f2227;
}

.ss-content {
  margin-top: 0 !important;
  border-radius: 2px;
  border-bottom-right-radius: 2px !important;
  border-bottom-left-radius: 2px !important;
  background: rgb(255, 255, 255);
  box-shadow: 0px 16px 24px 0px rgba(0, 0, 0, 0.06), 0px 2px 6px 0px rgba(0, 0, 0, 0.04), 0px 0px 1px 0px rgba(0, 0, 0, 0.04);
}
.ss-content .ss-option:not(.ss-hide) {
  display: block !important;
  border-radius: 2px;
  padding: 14px 16px !important;
  color: #2f2227 !important;
  font-size: 14px;
  line-height: 20px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.ss-content .ss-option:not(.ss-hide):hover {
  background-color: #e6f0f8 !important;
}
.ss-content .ss-option:not(.ss-hide).ss-selected {
  background-color: #e6f0f8 !important;
}
.ss-content .ss-list::-webkit-scrollbar {
  width: 4px;
}
.ss-content .ss-list::-webkit-scrollbar-thumb {
  border-radius: 10px;
  border: 2px solid rgba(0, 0, 0, 0.16);
}
.ss-content .ss-list::-webkit-scrollbar-track {
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
}

.pagination {
  display: flex;
  flex-wrap: nowrap;
  gap: 4px;
  padding: 16px 0;
  margin-left: auto;
  margin-right: auto;
}
.pagination__button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  border: none;
  border-radius: 8px;
  background-color: transparent;
  width: 40px;
  height: 40px;
  cursor: pointer;
  color: #0190db;
  text-align: center;
  font-size: 16px;
  line-height: 24px;
  transition: all 0.2s;
}
.pagination__button:hover:not(.index-pagination__button_disabled) {
  color: #006fbc;
}
.pagination__button_active {
  color: #5c5c5c;
  background-color: rgba(1, 144, 219, 0.08);
}
.pagination__button_disabled {
  color: #8f8f8f;
  cursor: default;
}
.pagination__icon {
  width: 24px;
  height: 24px;
  fill: currentColor;
}
.pagination__icon_right {
  transform: rotate(180deg);
}

.content {
  display: flex;
  flex-direction: column;
  gap: 32px;
  max-width: 1200px;
  width: 100%;
  margin: 80px auto 0;
  padding: 24px;
}
@media (max-width: 1199px) {
  .content {
    margin-bottom: 72px;
  }
}
@media (max-width: 767px) {
  .content {
    padding: 24px 16px;
    margin-bottom: 76px;
  }
}

.content-title {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.content-title__title-wrapper {
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.content-title__title {
  margin: 0;
  font-size: 24px;
  font-weight: 900;
  line-height: 32px;
  letter-spacing: -0.48px;
}
.content-title__sub-title {
  display: flex;
  gap: 4px;
  font-size: 16px;
  line-height: 24px;
}
.content-title__link {
  text-decoration: none;
  font-size: 16px;
  line-height: 24px;
  color: #006bb8;
}
.content-title__balance {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 96px;
  line-height: 112px;
  letter-spacing: -5.76px;
}
@media (max-width: 767px) {
  .content-title__balance {
    gap: 4px;
    font-size: 48px;
    line-height: 58px;
    letter-spacing: -1.92px;
  }
}
.content-title__icon {
  flex-shrink: 0;
  width: 84px;
  height: 84px;
}
@media (max-width: 767px) {
  .content-title__icon {
    width: 42px;
    height: 42px;
  }
}

.transfer {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.transfer__title {
  font-size: 24px;
  font-weight: 900;
  line-height: 32px;
  letter-spacing: -0.48px;
}
.transfer__cards {
  display: flex;
  gap: 16px;
  flex-wrap: wrap;
}
@media (max-width: 767px) {
  .transfer__cards {
    flex-direction: column;
  }
}

.transfer-card {
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  gap: 8px;
  width: 240px;
  padding: 16px 24px;
  border-radius: 24px;
}
@media (max-width: 1199px) {
  .transfer-card {
    width: 229px;
  }
}
@media (max-width: 767px) {
  .transfer-card {
    width: 100%;
  }
}
.transfer-card_accrued {
  background-color: #e9f8ef;
}
.transfer-card_accrued .transfer-card__title {
  color: #1b823f;
}
.transfer-card_accrued .transfer-card__title::before {
  background-image: url("../img/plus.svg");
}
.transfer-card_spent {
  background-color: #e6f0f8;
}
.transfer-card_spent .transfer-card__title {
  color: #005693;
}
.transfer-card_spent .transfer-card__title::before {
  background-image: url("../img/check.svg");
}
.transfer-card_burned {
  background-color: #fee;
}
.transfer-card_burned .transfer-card__title {
  color: #cc4347;
}
.transfer-card_burned .transfer-card__title::before {
  background-image: url("../img/fire.svg");
}
.transfer-card__title {
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  gap: 4px;
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
}
.transfer-card__title::before {
  flex-shrink: 0;
  content: "";
  display: block;
  width: 24px;
  height: 24px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%;
}
.transfer-card__balance {
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  gap: 2px;
  font-size: 24px;
  font-weight: 500;
  line-height: 32px;
  letter-spacing: -0.48px;
}
.transfer-card__balance::after {
  flex-shrink: 0;
  content: "";
  display: block;
  width: 24px;
  height: 24px;
  background-image: url("../img/coin.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%;
}

.filters {
  display: flex;
  gap: 4px;
}

.table-history {
  display: grid;
  grid-template-columns: 128fr 160fr 240fr 184fr 440fr;
}
.table-history__head {
  display: contents;
}
.table-history__head .table-cell-h:nth-child(1) {
  padding-left: 0;
}
@media (max-width: 767px) {
  .table-history__head {
    display: none;
    visibility: hidden;
  }
}
.table-history__cell-h {
  display: flex;
  align-items: center;
  padding: 16px 8px 14px;
  border-bottom: 2px solid rgba(0, 0, 0, 0.16);
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
}
.table-history__cell-h_right {
  justify-content: flex-end;
}
.table-history__row {
  display: contents;
  padding: 4px 0;
}
.table-history__row .table-cell:nth-child(1) {
  padding-left: 0;
}
.table-history__cell {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #d6d6d6;
  padding: 12px 8px;
  color: #1f1f1f;
  font-size: 14px;
  line-height: 20px;
}
@media (max-width: 767px) {
  .table-history__cell {
    border-bottom: none;
  }
}
.table-history__cell.align-start {
  align-items: flex-start;
}
.table-history__cell.ellipsis {
  display: block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

* {
  box-sizing: border-box;
}

html {
  -webkit-font-smoothing: antialiased;
  min-height: 100%;
}

body {
  overflow-x: hidden;
  font-size: 20px;
  line-height: 1.6;
  font-weight: 400;
  font-family: "Inter", "Helvetica", sans-serif;
  color: #2f2227;
  background-color: rgb(255, 255, 255);
}
@media (max-width: 767px) {
  body {
    font-size: 16px;
    line-height: 24px;
  }
}

.main {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.hidden {
  display: none !important;
}

.transparent {
  opacity: 0 !important;
}

.body-lock {
  overflow: hidden;
}

@media (max-width: 767px) {
  .mobile-none {
    display: none;
  }
}